import React from 'react'
import './App.css'
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles'
import Main from './Main'

const darkTheme = createMuiTheme({
  palette: {
    type: 'dark',
    primary: {
      light: '#757ce8',
      main: '#3f5776',
      dark: '#002877',
      contrastText: '#fff',
    },
    secondary: {
      light: '#ff7961',
      main: '#f44776',
      dark: '#ba000d',
      contrastText: '#000',
    },
  },
})

darkTheme.typography.h4 = {
  fontSize: '1.2rem',
  '@media (min-width:600px)': {
    fontSize: '1.2rem',
  },
  '@media (max-width:600px)': {
    fontSize: '1.0rem',
  },
  [darkTheme.breakpoints.up('md')]: {
    fontSize: '1.8rem',
  },
}

darkTheme.typography.h6 = {
  fontSize: '1.0rem',
  '@media (min-width:600px)': {
    fontSize: '1.1rem',
  },
  [darkTheme.breakpoints.up('md')]: {
    fontSize: '1.7rem',
  },
}

function App() {
  return (
    <ThemeProvider theme={darkTheme}>
      <div className="App">
        <Main></Main>
      </div>
    </ThemeProvider>
  )
}

export default App
