import React, { useContext, useState, useEffect } from 'react'
import { useTheme } from '@material-ui/core/styles'
import {
  BarChart,
  Bar,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  LabelList,
  Rectangle,
  ResponsiveContainer,
} from 'recharts'
import Title from './Title'
import { Appcontext } from '../AppContext'

const renderCustomizedLabel = (props) => {
  const { x, y, width, height, value } = props
  const radius = 10

  return (
    <g>
      {/* <circle cx={x + width} cy={y + 2*radius} r={radius} fill="#fff" /> */}
      <text
        x={x + width + 20}
        y={y + 2 * radius}
        fill="#fff"
        textAnchor="middle"
        dominantBaseline="middle"
      >
        {value}
      </text>
    </g>
  )
}

interface IStateChartProps {
  title: string
}

export default function StateChartH(props: IStateChartProps) {
  const theme = useTheme()
  const appContext = useContext(Appcontext)
  const [data, setData] = useState()

  const [date, setDate] = useState('')
  useEffect(() => {
    let startDate = '14-Mar-20'

    var handle = setInterval(() => {
      let tDate = new Intl.DateTimeFormat('en-GB', {
        year: 'numeric',
        month: 'short',
        day: '2-digit',
      }).format(new Date(startDate))

      if (appContext?.stateDateWise[tDate]) {
        setData(
          appContext?.stateDateWise[tDate].data.sort(
            (a, b) => b.count - a.count,
          ),
        )
        setDate(tDate)

        let tempstartDate = new Date(tDate)
        tempstartDate.setDate(tempstartDate.getDate() + 2)

        const today = new Intl.DateTimeFormat('en-GB', {
          year: 'numeric',
          month: 'short',
          day: '2-digit',
        }).format(new Date())

        const formatDate = new Intl.DateTimeFormat('en-GB', {
          year: 'numeric',
          month: 'short',
          day: '2-digit',
        }).format(tempstartDate)

        if (formatDate === today) {
          clearInterval(handle)
        }
        startDate = tempstartDate.toString()
      } else {
        clearInterval(handle)
      }
    }, 300)

    return () => clearInterval(handle)
  }, [])

  return (
    <React.Fragment>
      <Title>Statewise Cases as on {date}</Title>
      <ResponsiveContainer width="99%" height={600}>
        <BarChart
          height={600}
          layout="vertical"
          data={data}
          margin={{
            top: 5,
            right: 50,
            left: 50,
            bottom: 5,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" horizontal={false} />

          <XAxis
            type="number"
            dataKey="count"
            axisLine={true}
            stroke="#a0a0a0"
            domain={[0, 'dataMax + 1000']}
          />
          <YAxis type="category" dataKey="state" width={40} />

          <Tooltip />
          <Legend />
          <Bar dataKey="count" fill="#6495ED" minPointSize={0}>
            <LabelList dataKey="count" content={renderCustomizedLabel} />
          </Bar>
        </BarChart>
      </ResponsiveContainer>
    </React.Fragment>
  )
}
