import React, { Fragment, Children } from 'react'
import { AppContextInterface, AppContextProvider } from '../AppContext'
import { Constants } from './../statemap'
import { IStatewise } from '../models/IStatewise'
import axios from 'axios'

interface DataProps {
  children: any
}

interface DataState {
  isLoaded: boolean
  items: any
  error: { message: '' }
  totalcases: number
  totalrecovered: number
  totaldeceased: number
  totalActive: number
  statewise: any
  statescount: any
  deltaconfirmed: any
  deltaactive: any
  deltadeceased: any
  deltarecovered: any
  timeforeach5000: any
  allstatescount: any
  statesrecovered: any
  stateRowStats: any
  stateDateWise: any
}

export default class DataProvider extends React.Component<
  DataProps,
  DataState
> {
  state: DataState = {
    isLoaded: false,
    items: [],
    error: { message: '' },
    totalcases: 0,
    statewise: [],
    statescount: [],
    totalrecovered: 0,
    totaldeceased: 0,
    totalActive: 0,
    deltaactive: 0,
    deltaconfirmed: 0,
    deltadeceased: 0,
    deltarecovered: 0,
    timeforeach5000: [],
    allstatescount: 0,
    statesrecovered: [],
    stateRowStats: [],
    stateDateWise: [],
  }

  constructor(props: DataProps) {
    super(props)
    this.state = {
      error: { message: '' },
      isLoaded: false,
      items: [],
      totalcases: 0,
      statewise: [],
      statescount: [],
      totalrecovered: 0,
      totaldeceased: 0,
      totalActive: 0,
      deltaactive: 0,
      deltaconfirmed: 0,
      deltadeceased: 0,
      deltarecovered: 0,
      timeforeach5000: [],
      allstatescount: 0,
      statesrecovered: [],
      stateRowStats: [],
      stateDateWise: [],
    }
  }

  formatResponse(response: any): IStatewise {
    return {
      active: response.active,
      confirmed: response.confirmed,
      deaths: response.deaths,
      deltaconfirmed: response.deltaconfirmed,
      deltadeaths: response.deltadeaths,
      deltarecovered: response.deltarecovered,
      lastupdatedtime: response.lastupdatedtime,
      recovered: response.recovered,
      state: response.state,
      statecode: response.statecode,
      statenotes: response.statenotes,
    }
  }

  componentDidMount() {
    fetch('https://api.covid19india.org/states_daily.json')
      .then((res) => res.json())
      .then(
        (result) => {
          let stateDateWise = new Array()
          let statewise = new Array()
          let statesarray = new Array()
          let statesdata = result.states_daily
          let countrysum = 0
          let countryactive = 0
          let countryrecovered = 0
          let countrydeceased = 0
          let timeforeach5000 = new Array()
          let iMark = 1
          let iDays = 0
          let statewiserecovered = new Array()
          let statewiseStats = new Array()
          const stateCodeForMap = [
            'mh',
            'tg',
            'tn',
            'gj',
            'dl',
            'up',
            'ap',
            'br',
            'or',
            'rj',
            'jk',
            'pb',
            'ka',
            'kl',
            'mp',
          ]
          let prevDate = ''
          for (let i = 0; i < statesdata.length; i++) {
            if (statesdata[i]['status'] == 'Confirmed') {
              let tDate = new Intl.DateTimeFormat('en-GB', {
                year: 'numeric',
                month: 'short',
                day: '2-digit',
              }).format(new Date(statesdata[i]['date']))
              stateDateWise[tDate] = { data: [] }

              for (let j = 0; j < stateCodeForMap.length; j++) {
                const stateCode = new Constants().getState(stateCodeForMap[j])
                let currentRecord = 0
                if (stateDateWise[prevDate]) {
                  debugger
                  const prevRecord = stateDateWise[prevDate].data.filter(
                    (x) => x['state'] == stateCode,
                  )
                  currentRecord =
                    prevRecord.length == 0 ? 0 : prevRecord[0].count
                }
                stateDateWise[tDate].data.push({
                  state: new Constants().getState(stateCodeForMap[j]),
                  count:
                    currentRecord + parseInt(statesdata[i][stateCodeForMap[j]]),
                })
              }
              // stateDateWise[tDate].data.push({state: new Constants().getState('mh'), count:  parseInt(statesdata[i]['mh'])})
              // stateDateWise[tDate].data.push({state: new Constants().getState('tg'), count:  parseInt(statesdata[i]['tg'])} )
              // stateDateWise[tDate].data.push({state: new Constants().getState('tn'), count:  parseInt(statesdata[i]['tn'])} )
              // stateDateWise[tDate].data.push({state: new Constants().getState('gj'), count:  parseInt(statesdata[i]['gj'])} )
              // stateDateWise[tDate].data.push({state: new Constants().getState('dl'), count:  parseInt(statesdata[i]['dl'])} )
              // stateDateWise[tDate].data.push({state: new Constants().getState('up'), count:  parseInt(statesdata[i]['up'])} )
              // stateDateWise[tDate].data.push({state: new Constants().getState('ap'), count:  parseInt(statesdata[i]['ap'])} )
              // stateDateWise[tDate].data.push({state: new Constants().getState('br'), count:  parseInt(statesdata[i]['br'])} )
              // stateDateWise[tDate].data.push({state: new Constants().getState('or'), count:  parseInt(statesdata[i]['or'])} )
              // stateDateWise[tDate].data.push({state: new Constants().getState('rj'), count:  parseInt(statesdata[i]['rj'])} )
              // stateDateWise[tDate].data.push({state: new Constants().getState('jk'), count:  parseInt(statesdata[i]['jk'])} )
              // stateDateWise[tDate].data.push({state: new Constants().getState('pb'), count:  parseInt(statesdata[i]['pb'])} )

              prevDate = new Intl.DateTimeFormat('en-GB', {
                year: 'numeric',
                month: 'short',
                day: '2-digit',
              }).format(new Date(statesdata[i]['date']))
            }

            // For linear graph
            const date1: Date = new Date(statesdata[i]['date'])
            const date2: Date = new Date()
            const diffTime: any = date2.valueOf() - date1.valueOf()
            const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))

            if (date1 <= date2) {
              if (statesdata[i]['status'] == 'Confirmed') {
                countrysum = countrysum + parseInt(statesdata[i]['tt'])
                statesarray.push({
                  total: countrysum,
                  date: statesdata[i]['date'],
                })
                if (
                  countrysum >= 20000 &&
                  countrysum % (iMark * 20000) < 19999
                ) {
                  timeforeach5000.push({ mark: countrysum, interval: iDays })
                  iDays = 1
                  iMark++
                }
                iDays++
              }

              switch (statesdata[i]['status']) {
                case 'Recovered':
                  countryrecovered =
                    countryrecovered + parseInt(statesdata[i]['tt'])
                  break
                case 'Deceased':
                  countrydeceased =
                    countrydeceased + parseInt(statesdata[i]['tt'])
                  break
              }
            }

            //for datewuise graph
            if (diffDays <= 10 && date1 <= date2) {
              if (statesdata[i]['status'] == 'Confirmed') {
                statewise.push({
                  count: parseInt(statesdata[i]['tt']),
                  date: statesdata[i]['date'],
                })
              }
            }

            if (diffDays <= 30 && date1 <= date2) {
              if (
                statesdata[i]['status'] == 'Confirmed' ||
                statesdata[i]['status'] == 'Recovered'
              ) {
                const existState = statewiserecovered.filter(
                  (x) => x['date'] == statesdata[i]['date'],
                )

                if (existState && existState.length > 0) {
                  if (statesdata[i]['status'] == 'Confirmed') {
                    existState[0].confirmed = countrysum
                  } else if (statesdata[i]['status'] == 'Recovered') {
                    existState[0].recovered = countryrecovered
                    existState[0].active =
                      countrysum - countryrecovered - countrydeceased
                  }
                } else {
                  if (statesdata[i]['status'] == 'Confirmed') {
                    statewiserecovered.push({
                      recovered: 0,
                      confirmed: countrysum,
                      date: statesdata[i]['date'],
                    })
                  } else if (statesdata[i]['status'] == 'Recovered') {
                    statewiserecovered.push({
                      active: 0,
                      recovered: countryrecovered,
                      date: statesdata[i]['date'],
                    })
                  }
                }
              }
            }
          }

          let keysArray = Object.keys(statesdata[0])

          //aggregate all the values for each row
          const statescount = statesdata.reduce(
            function (res, obj) {
              for (let i = 0; i < keysArray.length; i++) {
                const prop = keysArray[i]
                if (prop != 'date' && prop != 'tt' && prop != 'status') {
                  if (obj['status'] == 'Confirmed') {
                    let propsum = parseInt(obj[prop])
                    let curRecovered = 0
                    if (res[prop]) {
                      propsum =
                        res[prop].confirmed +
                        (isNaN(parseInt(obj[prop])) ? 0 : parseInt(obj[prop]))
                      curRecovered = res[prop].recovered ?? 0
                    }
                    res[prop] = {
                      confirmed: propsum,
                      recovered: curRecovered,
                    }
                  } else if (obj['status'] == 'Recovered') {
                    let propsumrec = parseInt(obj[prop])
                    if (res[prop]) {
                      propsumrec = res[prop].recovered + parseInt(obj[prop])
                    }
                    res[prop] = {
                      confirmed: res[prop].confirmed,
                      recovered: propsumrec,
                    }
                  }
                }
              }
              return res
            },
            { __array: [] },
          )

          const yesterdayConfirmedStats = statesdata[statesdata.length - 3]
          const yesterdayRecoveredStats = statesdata[statesdata.length - 2]

          for (let j = 0; j < keysArray.length; j++) {
            const prop = keysArray[j]
            if (prop != 'date' && prop != 'tt' && prop != 'status') {
              statescount[prop] = {
                confirmed: statescount[prop].confirmed,
                recovered: statescount[prop].recovered,
                yesterdayConfirmed: parseInt(yesterdayConfirmedStats[prop]),
                yesterdayRecovered: parseInt(yesterdayRecoveredStats[prop]),
              }
            }
          }

          function createData(
            name,
            confirmed,
            recovered,
            yesterdayConfirmed,
            yesterdayRecovered,
          ) {
            return {
              name,
              confirmed,
              recovered,
              yesterdayConfirmed,
              yesterdayRecovered,
            }
          }

          let stateRowStats = new Array()

          for (let key in statescount) {
            if (statescount[key] && key != '__array') {
              stateRowStats.push(
                createData(
                  new Constants().getState(key),
                  statescount[key].confirmed,
                  statescount[key].recovered,
                  statescount[key].yesterdayConfirmed,
                  statescount[key].yesterdayRecovered,
                ),
              )
            }
          }

          axios
            .get<any>('https://api.covid19india.org/data.json')
            .then((response) => {
              const _response = response.data.statewise.map((statewise: any) =>
                this.formatResponse(statewise),
              )
              let _statescount: any = []
              let _allstatescount: any = []

              for (let i = 0; i < _response.length; i++) {
                if (
                  _response[i].confirmed > 700 &&
                  _response[i].statecode != 'TT'
                ) {
                  _statescount.push({
                    state: _response[i].state,
                    count: parseInt(_response[i].confirmed),
                  })
                }

                if (_response[i].statecode != 'TT') {
                  _allstatescount.push({
                    state: _response[i].state,
                    count: parseInt(_response[i].active),
                  })
                }
              }

              this.setState({
                isLoaded: true,
                totalcases: _response[0].confirmed,
                totalActive: _response[0].active,
                totaldeceased: _response[0].deaths,
                totalrecovered: _response[0].recovered,
                statescount: _statescount,
                deltarecovered: _response[0].deltarecovered,
                deltadeceased: _response[0].deltadeaths,
                deltaconfirmed: _response[0].deltaconfirmed,
                deltaactive: _response[0].deltaactive,
                allstatescount: _allstatescount,
              })
            })

          //This is to exclude today from the list.
          timeforeach5000[timeforeach5000.length - 1]['interval'] =
            timeforeach5000[timeforeach5000.length - 1]['interval'] - 1

          this.setState({
            isLoaded: true,
            items: statesarray,
            statewise: statewise,
            timeforeach5000: timeforeach5000,
            statesrecovered: statewiserecovered,
            stateRowStats: stateRowStats,
            stateDateWise: stateDateWise,
          })
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          this.setState({
            isLoaded: true,
            error,
          })
        },
      )
  }

  render() {
    const {
      error,
      isLoaded,
      items,
      totalcases,
      statewise,
      statescount,
      totalActive,
      totaldeceased,
      totalrecovered,
      deltaconfirmed,
      deltaactive,
      deltadeceased,
      deltarecovered,
      timeforeach5000,
      allstatescount,
      statesrecovered,
      stateRowStats,
      stateDateWise,
    } = this.state

    const chartAppContext: AppContextInterface = {
      data: items,
      total: totalcases,
      statewise: statewise,
      statescount: statescount,
      totalActive,
      totaldeceased,
      totalrecovered,
      deltaconfirmed,
      deltarecovered,
      deltaactive,
      deltadeceased,
      timeforeach5000,
      allstatescount,
      statesrecovered,
      stateRowStats,
      stateDateWise,
    }
    if (error.message != '') {
      return <div>Error: {error.message}</div>
    } else if (!isLoaded) {
      return <div>Loading...</div>
    } else {
      return (
        <AppContextProvider value={chartAppContext}>
          <Fragment>{this.props.children}</Fragment>
        </AppContextProvider>
      )
    }
  }
}
