import React, { useContext } from 'react'
import { useTheme } from '@material-ui/core/styles'
import {
  BarChart,
  Bar,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  LabelList,
  ResponsiveContainer,
} from 'recharts'
import Title from './Title'
import { Appcontext } from '../AppContext'

const renderCustomizedLabel = (props) => {
  const { x, y, width, height, value } = props
  const radius = 10

  return (
    <g>
      <circle cx={x + width / 2} cy={y - radius} r={radius} fill="#fff" />
      <text
        x={x + width / 2}
        y={y - radius}
        fill="#3f5776"
        textAnchor="middle"
        dominantBaseline="middle"
      >
        {value}
      </text>
    </g>
  )
}

const getColor = (props) => {
  return '#6495ED'
}

export default function TimeChart() {
  const appContext = useContext(Appcontext)
  return (
    <React.Fragment>
      <Title>{`Days for each 20000 cases`}</Title>
      <ResponsiveContainer width="99%" height={400}>
        <BarChart
          data={appContext?.timeforeach5000}
          height={400}
          margin={{
            top: 5,
            right: 30,
            left: 20,
            bottom: 5,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="mark" />
          <YAxis dataKey="interval" domain={[0, 'dataMax + 10']} />
          <Tooltip />
          <Legend />
          <Bar dataKey="interval" fill={getColor(1)} minPointSize={0}>
            <LabelList dataKey="interval" content={renderCustomizedLabel} />
          </Bar>
        </BarChart>
      </ResponsiveContainer>
    </React.Fragment>
  )
}
