import React, { useContext } from 'react'
import Link from '@material-ui/core/Link'
import { makeStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'
import Title from './Title'
import { Appcontext } from '../AppContext'

interface IProps {
  type: number
}

function preventDefault(event: any) {
  event.preventDefault()
}

const useStyles = makeStyles({
  depositContext: {
    flex: 1,
  },
})

export default function Totals(props: IProps) {
  const classes = useStyles()
  const appContext = useContext(Appcontext)
  let total: number | undefined = 0
  let title = ''
  let delta = 0
  let colortext = ''
  switch (props.type) {
    case 1:
      total = appContext?.total
      title = 'Confirmed'
      delta = appContext?.deltaconfirmed
      colortext = 'red-color-text'
      break
    case 2:
      total = appContext?.totalActive
      title = 'Active'
      delta = -1 //appContext?.deltaactive
      colortext = 'blue-color-text'
      break
    case 3:
      total = appContext?.totalrecovered
      title = 'Recovered'
      delta = appContext?.deltarecovered
      colortext = 'green-color-text'
      break
    case 4:
      total = appContext?.totaldeceased
      title = 'Deceased'
      delta = appContext?.deltadeceased
      colortext = 'gray-color-text'
      break
  }

  return (
    <React.Fragment>
      <Title>{title}</Title>
      <Typography component="p" variant="h4">
        {total}
      </Typography>
      {/* <Typography color="textSecondary" className={classes.depositContext}>
        on {new Date().toDateString()}
      </Typography> */}
      {delta > -1 ? (
        <Typography
          color="textSecondary"
          className={`${classes.depositContext} ${colortext}`}
        >
          +[{delta}]
        </Typography>
      ) : (
        <Typography
          color="textSecondary"
          className={`${classes.depositContext} visually-hidden`}
        >
          +[{delta}]
        </Typography>
      )}
    </React.Fragment>
  )
}
