import {
  Map as LeafletMap,
  CircleMarker,
  TileLayer,
  GeoJSON,
  Marker,
  Popup,
  FeatureGroup,
  Tooltip,
} from 'react-leaflet'
import React, { useContext, Fragment, Component } from 'react'
import { Appcontext } from '../AppContext'
import 'leaflet/dist/leaflet.css'
import data from './CitiesData'
import L from 'leaflet'
import axios from 'axios'
import hash from 'object-hash'
import Control from 'react-leaflet-control'
import { Typography } from '@material-ui/core'

interface GeoProps {
  data: any
}

interface GeoState {
  isLoaded: boolean
  items: any
  error: { message: '' }
  height: string
  width: string
  zoom: number
  mulfactor: number
}

class StateBubbleChart extends React.Component<GeoProps, GeoState> {
  _editableFG = { leafletElement: { addLayer: (_layer) => {} } }
  state: GeoState = {
    isLoaded: false,
    items: [],
    error: { message: '' },
    height: '0',
    width: '0',
    zoom: 5,
    mulfactor: 20,
  }
  constructor(props: GeoProps) {
    super(props)
    this.state = {
      error: { message: '' },
      isLoaded: false,
      items: [],
      height: '700px',
      width: '100%',
      zoom: 5,
      mulfactor: 15,
    }
  }

  updateDimensions() {
    const height = window.innerWidth >= 992 ? window.innerHeight : 400
    if (window.innerWidth < 600) {
      this.setState({ zoom: 4, mulfactor: 8, height: '500px' })
    }
  }

  componentWillMount() {
    this.updateDimensions()
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.updateDimensions.bind(this))
  }

  async componentDidMount() {
    const res = await axios.get('./data/custom.geo.json')

    const items = res.data

    const statewise = this.props.data
    debugger
    data.city.forEach((item) => {
      const eachState = statewise.find((x) => {
        if (x.state == item['name']) return x
      })
      if (eachState) {
        item.activecases = eachState.count
        if (eachState.count >= 5000) {
          item.color = '#FF7733'
        }
        if (eachState.count > 1000 && eachState.count < 5000) {
          item.color = '#FFE033'
        }
        if (eachState.count <= 1000) {
          item.color = '#33B2FF'
        }
      } else {
        item.activecases = 0
      }
    })

    this.setState({ items: items })

    window.addEventListener('resize', this.updateDimensions.bind(this))
  }

  _onFeatureGroupReady = (ref) => {
    if (ref === null) {
      return
    }
    this._editableFG = ref
    if (this._editableFG != null) {
      // populate the leaflet FeatureGroup with the geoJson layers
      if (this.state.items) {
        let leafletGeoJSON = new L.GeoJSON(this.state.items)
        if (this._editableFG != null) {
          let leafletFG = this._editableFG && this._editableFG.leafletElement
          if (leafletFG != null) {
            leafletGeoJSON.eachLayer((layer) => leafletFG.addLayer(layer))
          }
        }
      }
    }
  }

  render() {
    //const appContext = useContext(Appcontext)
    const { items, height, width, zoom, mulfactor } = this.state
    const s = hash(items)

    // let geoJSON = <></>

    // if (items != null && items.features != null) {
    //   geoJSON = (
    //     <GeoJSON
    //       key={hash(items)}
    //       data={items}
    //       style={() => ({
    //         color: '#4a83ec',
    //         weight: 0.5,
    //         fillColor: '#1a1d62',
    //         fillOpacity: 1,
    //       })}
    //     />
    //   )
    // }

    const redlabelStyle = {
      background: '#FF7733',
      height: '20px',
      width: '100px',
    }

    const bluelabelStyle = {
      background: '#33B2FF',
      height: '20px',
      width: '100px',
    }

    const orangelabelStyle = {
      background: '#FFE033',
      height: '20px',
      width: '100px',
      color: 'black',
    }

    return (
      <LeafletMap
        key={hash(items)}
        style={{ height: height, width: width }}
        center={[22.44212, 78.391384]}
        maxZoom={10}
        attributionControl={true}
        zoomControl={false}
        doubleClickZoom={false}
        scrollWheelZoom={false}
        dragging={false}
        animate={true}
        easeLinearity={0.35}
        zoom={zoom}
      >
        {/* {geoJSON} */}
        {/* <Marker position={[50, 10]}>
          <Popup>Popup for any custom information.</Popup>
        </Marker> */}
        <FeatureGroup
          ref={(reactFGref) => {
            this._onFeatureGroupReady(reactFGref)
          }}
        ></FeatureGroup>
        {data.city.map((city) => {
          return (
            <CircleMarker
              key={hash(city['name'])}
              center={[city['coordinates'][1], city['coordinates'][0]]}
              radius={
                mulfactor *
                Math.log(
                  city['activecases'] / (city['activecases'] > 200 ? 400 : 500),
                )
              }
              fillOpacity={0.5}
              stroke={false}
              fillColor={city['color']}
            >
              {/* <Popup>
                    <span>{city['name'] +
                    ': ' +
                    ' ' +
                    city['activecases']}</span>
              </Popup> */}
              <Tooltip direction="right" offset={[-8, -2]} opacity={1}>
                <span>{city['name'] + ': ' + ' ' + city['activecases']}</span>
              </Tooltip>
            </CircleMarker>
          )
        })}
        <Control position="topleft">
          <div className={`info legend`}>
            <Typography component="p" variant="h4">
              Legend
            </Typography>
            <div style={redlabelStyle}>
              <p>{'5000+'}</p>
            </div>
            <div style={orangelabelStyle}>
              <p>{'1000 - 5000'}</p>
            </div>
            <div style={bluelabelStyle}>
              <p>{'0 - 1000'}</p>
            </div>
          </div>
        </Control>
      </LeafletMap>
    )
  }
}

export default StateBubbleChart
