import React, { Fragment } from 'react'
import { AppContextInterface, AppContextProvider } from '../AppContext'
import { IStatewise } from '../models/IStatewise'
import axios from 'axios'

interface DataProps {
  statecode: any
  state: any
}

interface DataState {
  isLoaded: boolean
  items: any
  error: { message: '' }
  totalcases: number
  totalrecovered: number
  totaldeceased: number
  totalActive: number
  statewise: any
  statescount: any
  deltaconfirmed: any
  deltaactive: any
  deltadeceased: any
  deltarecovered: any
}

export default class StateDataProvider extends React.Component<
  DataProps,
  DataState
> {
  _props: DataProps = {
    state: 'Test',
    statecode: 'test',
  }
  state: DataState = {
    isLoaded: false,
    items: [],
    error: { message: '' },
    totalcases: 0,
    statewise: [],
    statescount: [],
    totalrecovered: 0,
    totaldeceased: 0,
    totalActive: 0,
    deltaactive: 0,
    deltaconfirmed: 0,
    deltadeceased: 0,
    deltarecovered: 0,
  }

  constructor(props: DataProps) {
    super(props)
    this._props = props
    this.state = {
      error: { message: '' },
      isLoaded: false,
      items: [],
      totalcases: 0,
      statewise: [],
      statescount: [],
      totalrecovered: 0,
      totaldeceased: 0,
      totalActive: 0,
      deltaactive: 0,
      deltaconfirmed: 0,
      deltadeceased: 0,
      deltarecovered: 0,
    }
  }

  formatResponse(response: any): IStatewise {
    return {
      active: response.active,
      confirmed: response.confirmed,
      deaths: response.deaths,
      deltaconfirmed: response.deltaconfirmed,
      deltadeaths: response.deltadeaths,
      deltarecovered: response.deltarecovered,
      lastupdatedtime: response.lastupdatedtime,
      recovered: response.recovered,
      state: response.state,
      statecode: response.statecode,
      statenotes: response.statenotes,
    }
  }

  componentDidMount() {
    fetch('https://api.covid19india.org/state_district_wise.json')
      .then((res) => res.json())
      .then(
        (result) => {
          const state = this._props.state
          const stateCode = this._props.statecode
          let statewise = new Array()
          let statesarray = new Array()
          let statesdata = result[state].districtData
          let _statescount: any = []

          let districts = Object.keys(statesdata)

          for (let i = 0; i < districts.length; i++) {
            const prop = districts[i]
            const count = parseInt(statesdata[prop].confirmed)
            if (count > 10)
              _statescount.push({
                state: prop,
                count: count,
              })
          }

          axios
            .get<any>('https://api.covid19india.org/data.json')
            .then((response) => {
              let stateIndex = 0
              const _response = response.data.statewise.map((statewise: any) =>
                this.formatResponse(statewise),
              )
              let _statescount: any = []

              for (let i = 0; i < _response.length; i++) {
                if (
                  _response[i].confirmed > 0 &&
                  _response[i].statecode != 'TT'
                ) {
                  if (_response[i].statecode == stateCode) {
                    stateIndex = i
                  }
                }
              }

              this.setState({
                isLoaded: true,
                totalcases: _response[stateIndex].confirmed,
                totalActive: _response[stateIndex].active,
                totaldeceased: _response[stateIndex].deaths,
                totalrecovered: _response[stateIndex].recovered,
                deltarecovered: _response[stateIndex].deltarecovered,
                deltadeceased: _response[stateIndex].deltadeaths,
                deltaconfirmed: _response[stateIndex].deltaconfirmed,
                deltaactive: _response[stateIndex].deltaactive,
              })
            })

          this.setState({
            isLoaded: true,
            items: statesarray,
            statewise: statewise,
            statescount: _statescount,
          })
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          this.setState({
            isLoaded: true,
            error,
          })
        },
      )
  }

  render() {
    const {
      error,
      isLoaded,
      items,
      totalcases,
      statewise,
      statescount,
      totalActive,
      totaldeceased,
      totalrecovered,
      deltaconfirmed,
      deltaactive,
      deltadeceased,
      deltarecovered,
    } = this.state

    const chartAppContext: AppContextInterface = {
      data: items,
      total: totalcases,
      statewise: statewise,
      statescount: statescount,
      totalActive,
      totaldeceased,
      totalrecovered,
      deltaconfirmed,
      deltarecovered,
      deltaactive,
      deltadeceased,
      timeforeach5000: [],
      allstatescount: 0,
      statesrecovered: [],
      stateRowStats: [],
      stateDateWise: [],
    }
    if (error.message != '') {
      return <div>Error: {error.message}</div>
    } else if (!isLoaded) {
      return <div>Loading...</div>
    } else {
      return (
        <AppContextProvider value={chartAppContext}>
          <Fragment>{this.props.children}</Fragment>
        </AppContextProvider>
      )
    }
  }
}
