import React, { useContext } from 'react'
import { useTheme } from '@material-ui/core/styles'
import {
  BarChart,
  Bar,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  LabelList,
  ResponsiveContainer,
} from 'recharts'
import Title from './Title'
import { Appcontext } from '../AppContext'

const renderCustomizedLabel = (props) => {
  const { x, y, width, height, value } = props
  const radius = 10

  return (
    <g>
      <circle cx={x + width / 2} cy={y - radius} r={0} />
      <text
        x={x + width / 2}
        y={y - radius}
        fill="#fff"
        textAnchor="middle"
        dominantBaseline="middle"
      >
        {value}
      </text>
    </g>
  )
}

export default function StateRecoveredChart() {
  const theme = useTheme()
  const appContext = useContext(Appcontext)
  const date1: Date = new Date(appContext?.statewise[0].date)
  const date2: Date = new Date()
  const diffTime: any = date2.valueOf() - date1.valueOf()
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
  //aggregate all the values for each row
  const statescount = appContext?.statewise.reduce(
    function (res, obj) {
      if (res['count'] == undefined) res['count'] = 0
      res['count'] += parseInt(obj['count'])
      return res
    },
    { __array: [] },
  )
  const avg = statescount['count'] / (diffDays - 1)
  const recoveredStyle = {
    background: '#82ca9d',
    height: '20px',
    width: '20px',
  }
  const confirmedStyle = {
    background: '#8884d8',
    height: '20px',
    width: '20px',
  }
  return (
    <React.Fragment>
      <Title>{`Recovered vs Active`}</Title>
      <ResponsiveContainer width="99%" height={400}>
        <BarChart
          data={appContext?.statesrecovered}
          height={400}
          margin={{
            top: 5,
            right: 30,
            left: 20,
            bottom: 5,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="date" />
          <YAxis domain={[0, 'dataMax + 2000']} />
          <Tooltip />
          <Legend>
            <div style={recoveredStyle}></div>
            <div style={confirmedStyle}></div>
          </Legend>
          <Bar dataKey="active" stackId="a" fill="#6495ED" minPointSize={0}>
            <LabelList dataKey="count" content={renderCustomizedLabel} />
          </Bar>
          <Bar dataKey="recovered" stackId="a" fill="#4CC417" minPointSize={0}>
            <LabelList dataKey="count" content={renderCustomizedLabel} />
          </Bar>
        </BarChart>
      </ResponsiveContainer>
    </React.Fragment>
  )
}
